var Utility = require("Utility");
cc.Class({
    extends: cc.Component,

    properties: {
        spine:{
            default:null,
            type:cc.Node
        },
        lbLose1:{
            default:null,
            type:cc.Label
        },
        lbLose2:{
            default:null,
            type:cc.Label
        },
        lbWin:{
            default:null,
            type:cc.Label
        },
        lbPot:{
            default:null,
            type:cc.Label
        },
        player1:{
            default:null,
            type:cc.Node
        },
        player2:{
            default:null,
            type:cc.Node
        }
    },

    ctor:function(){
        this.gameController = null;
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {

    },

    onEnable:function(){

        var emo = this.spine;
        var spine = emo.getComponent('sp.Skeleton');
        spine.clearTracks();
        spine.setCompleteListener(function(trackEntry){
            // spine.clearTracks();
            // emo.active = false;
        });
        emo.active = true;
        spine.setAnimation(0, 'animation', false);

        this.node.runAction(cc.sequence(
            cc.delayTime(3),
            cc.callFunc(function () {
                this.showNodeCHPlay();
            },this)
        ))
    },

    showNodeCHPlay:function(){
        this.gameController.showNodeCHPlay();
    },
    start () {

    },

    // update (dt) {},
});
